<template>
  <h1>{{ Object.keys(TheContent)[0] }}</h1>
  <div class="content-wrapper">
    <aside
      v-for="(mainTopic, title) in TheContent['JavaScript Beginner Guide']"
      :key="mainTopic"
    >
      <h2>{{ title }}</h2>
      <l-button
        v-for="(subTopic, subTitle) in mainTopic.topics"
        :key="subTitle"
        @click="
          shownCard = [subTitle, subTopic];
          consoleIt(shownCard);
        "
        >{{ subTitle }}</l-button
      >
    </aside>

    <teleport to=".card-plate-handler">
      <z-plate>
        <card>
          <template v-slot:h3>{{ shownCard[0] }}</template>
          <template v-slot:default><div v-html="shownCard[1]"></div></template>
        </card>
      </z-plate>
    </teleport>
  </div>
</template>

<script>
import card from "../components/Card.vue";
import ZPlate from "../components/ZPlate.vue";
import LButton from "../components/LButton.vue";
export default {
  name: "content",
  components: { card, ZPlate, LButton },
  data() {
    return {
      TheContent: {},
      shownCard: [],
    };
  },
  methods: {
    getContent() {
      fetch("./content.json")
        .then((r) => r.json())
        .then((l) => (this.TheContent = l))
        .catch((err) => alert(err));
    },
    consoleIt(y) {
      console.log(y);
    },
  },
  created() {
    this.getContent();
  },
};
</script>

